@import './fonts.css';

*,
:before,
:after {
    box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

body {
    font-family: 'Roboto', sans-serif;
}

a {
    text-decoration: none;
}

button {
    cursor: pointer;
    font-family: inherit;
}

li {
    list-style: none;
}

ul {
    margin: 0;
    padding: 0;
}

.MuiAutocomplete-root {
    width: 100%;
}

