/* menu  */
.menu-enter {
    margin-left: -100vw;
}

.menu-enter-active {
    margin-left: 0;
    transition: margin-left 300ms ease-out;
}

.menu-exit {
    margin-left: 0;
}

.menu-exit-active {
    margin-left: -100vw;
    ;
    transition: margin-left 300ms ease-out;
}

/* scale */
.scale-enter {
    transform: scale(0);
}

.scale-enter-active {
    transform: scale(1);
    transition: transform 300ms ease-out;
}

.scale-exit {
    transform: scaleY(1);

}

.scale-exit-active {
    transform: scaleY(0);
    transition: transform 300ms ease-out;
}